<template>
  <v-layout row wrap>
    <br>
    Hello
    {{pract}}
  </v-layout>
</template>

<script>
export default {
  data: () => ({
    pract: []
  }),
  methods: {
    getPractitioners() {
      fetch("/fhir/Practitioner?_count=10").then(response => {
        response.json().then(data => {
          this.pract = data
        });
      })
    }
  },
  created() {
    this.getPractitioners()
  }
}
</script>